.header {
	background-size: cover;
	position: relative;

	&__inner{
		min-height: 500px;
		margin: 0 auto;
		display: flex;
		align-items: center;
		width: $container-xs;

		@media screen and (min-width: $screen-sm) {
			width: $container-sm;
			position: relative;
		}

		@media screen and (min-width: $screen-lg) {
			width: $container-lg;
		}

	}

}


.main-nav{
	position: absolute;
	top: 0;
	left: 0;
	transform: translate(-100%, 0);
	height: 100%;
	background-color: #1c1531;
	display: flex;
	align-items: center;
	transition: all 0.325s 0s ease;
	// display: none;

	@media screen and (min-width: $screen-sm) {

		width: 100%;
		top: 35px;
		height: auto;
		transform: translate(0, 0);
		background-color: rgba(30, 20, 50, 0.5);
	}

	&--active {
		color: #fff;
	}
	

	&--open{
		transform: translate(0, 0);
	}

	&__list{
		padding: 0 30px;
		margin: 0;
		list-style-type: none;

		@media screen and (min-width: $screen-sm) {
			width: 100%;
			display: flex;
			flex-wrap: wrap;
			justify-content: space-between;
		}
	}

}
.main-nav__link {
	color: #fff;
	text-decoration: none;
	font-family: $ff-Jura;
	font-size: $sz-14;
	line-height: $lh-36;
	&--active {
		color: #fff;
	}
	@media screen and (min-width: $screen-lg) {
	font-size: $sz-16;
	}
}

.main-title{


	@media screen and (min-width: $screen-sm) {
		padding-left: 50px;
	}

	&__head{
		margin: 0;
		color: #fff;
		text-decoration: none;
		font-family: $ff-Nord;

		@media screen and (min-width: $screen-lg) {
		font-size: 50px;
	}
	}
	&__description {
		font-family: $ff-Jura;
		font-size: $sz-16;
		color: $c-white;
		font-weight: 300;
	}
	@media screen and (min-width: $screen-sm) {
		font-size: $sz-20;
	}
}



.side-bar{
	position: absolute;
	top: 0;
	right: 0;
	background-color: #1c1531;
	opacity: 0.5;
	color: #fff;
	height: 100%;
	width: 40px;
	display: flex;
	align-items: center;
	justify-content: center;
	white-space: nowrap;
	z-index: 10;

	@media screen and (min-width: $screen-sm) {
		display: none;
	}

	&__vk{
		@include sprite($vk);
		position: absolute;
		top: 10px;
	}

	&__link{
			transform: rotate(-90deg);
			color: $c-white;
	}
}
.nav-burger{
		position: absolute;
		top: 0;
		left: 0;
		z-index: 9;
		margin: 10px;
	@media screen and (min-width: $screen-sm) {
		display: none;
	}
	&--close{
		@include sprite($close);
	}
	&--hamburger{
		@include sprite($burger);
	}
}